import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { take, map, catchError } from 'rxjs/operators';
import { ListsService } from '@modules/lists/shared';
import { Library, Helper, sEnum, AuthService } from '@core/index';
import { forkJoin } from 'rxjs';
import { VatService } from '@modules/vat/shared';
import { UsersService } from '@modules/users/shared';
@Injectable()
export class TicketResolver implements Resolve<any[]> {
    constructor(
        private vatService: VatService,
        private listsService: ListsService,
        private router: Router,
        private authService: AuthService,
    ) { }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
        Library.showloading();
        let _countDm = 0;
        let dataJoin = [];
        let dataDM: any;
        let invoice_id = route.paramMap.get('invoiceId');
        let arr = [
            {
                "code": "DM_NHOM_KHACH_HANG",
                "order": 0
            },
            {
                "code": "DM_LOAI_TIEN",
                "order": 0
            },
            {
                "code": "DM_HINH_THUC_THANH_TOAN",
                "order": 0
            },
            {
                "code": "DM_DON_VI_TINH",
                "order": 0
            },
            {
                "code": "DM_TY_LE_VAT",
                "order": 0
            },
            {
                "code": "DM_DINH_KHOAN",
                "order": 0
            },
            {
                "code": "DM_NOI_XUAT_HOA_DON",
                "order": 0
            },
            {
                "code": "DM_QUOC_GIA",
                "order": 0
            },
            {
                "code": "DM_TAI_KHOAN_NGAN_HANG",
                "order": 0
            },
        ];
        dataDM = this.listsService.getDMBy(arr);
        if (dataDM.length > 0) {
            dataDM.forEach(element => {
                dataJoin.push(element);
            });
        }
        _countDm = dataDM.length;
        let isEdit = false;
        if (invoice_id)
            isEdit = true;
        let dataRecord = this.vatService.getTicketResolver(invoice_id);
        if (dataRecord.length > 0) {
            dataRecord.forEach(element => {
                dataJoin.push(element);
            });
        }
        let dataForkJoin = forkJoin(dataJoin);

        return dataForkJoin.pipe(
            take(1),
            map(rs => {
                Library.hideloading();
                let status = sEnum.statusApi.success;
                for (let index = 0; index < rs.length; index++) {
                    if (rs[index].status != sEnum.statusApi.success) {
                        Library.notify(rs[index].message, "error");
                        this.router.navigate(['/system/ticket-invoice']);
                        status = sEnum.statusApi.error;
                        break;
                    }
                }
                if (status == sEnum.statusApi.success) {
                    if (dataDM.length > 0) {
                        let _data = [];
                        rs.forEach((element, i) => {
                            if (i < dataDM.length) {
                                _data.push(element);
                            }
                        });
                        this.authService.setDMLocal(_data);
                    }
                    let templates;
                    if (isEdit) {
                        templates = Helper.convertTemplatesToInvoiceResolver(rs[dataJoin.length - 2]);
                        return [
                            {
                                products: rs[_countDm].data.products,
                                templates: templates,
                                isEdit: isEdit,
                                invoices: rs[dataJoin.length - 1].data.invoice
                            }

                        ];
                    }
                    else {
                        templates = Helper.convertTemplatesToInvoiceResolver(rs[dataJoin.length - 1]);
                        return [
                            {
                                products: rs[_countDm].data.products,
                                templates: templates,
                                isEdit: isEdit,
                            }
                        ];
                    }
                }
                else
                    return null;
            })
        );
    }
}
@Injectable()
export class TicketResolverIndex implements Resolve<any[]> {
    constructor(
        private vatService: VatService,
        private usersService: UsersService,
    ) { }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
        return this.usersService.getAllUser('?fields[]=id&fields[]=name&')
    }
}