import { Injectable } from '@angular/core';
import * as QRCode from 'qrcode';
import * as moment from 'moment';
import { TranslateService } from '@ngx-translate/core';
import { AuthService } from '@core/services';

@Injectable({
  providedIn: 'root'
})
export class TicketService {
  invoice_date = '';
  _translate: any;
  _language: string;
  configs: any = {};

  constructor(
    public translate: TranslateService,
    private authService: AuthService,
  ) {
    this._translate = this.authService.getTranslate();
    this._language = this.authService.getLanguage();
    translate.use(this._language);
  }

  /**
   * 
   * @param invoice 
   * @param invoiceTemplate 
   * @param is_multi_product: V l loi 1 sp hay nhiu sp
   * @param options Cc tu chn: 
   *    - inVe: in v ra my in nhit
   *    - inChuyenDoi: In chuyn i
   *    - isPreview: Mn xem trc?
   * @returns 
   */
  createFormTypeBase64(invoice, invoiceTemplate, is_multi_product, options: any = {}) {
    let rawHtml = JSON.parse(Base64.decode(invoiceTemplate.raw_file));
    rawHtml = rawHtml.html + '</html>';
    this.invoice_date = ''
    const parser = new DOMParser();
    const doc = parser.parseFromString(rawHtml, 'text/html');
    // test nh
    // const divBackground:any = doc.querySelector('.logo-template-right');
    // if(divBackground) {
    //   let url_image:string = divBackground.style.backgroundImage;
    //   url_image = url_image.slice(4, -1).replace(/["']/g, '');
    //   let img:any = doc.createElement('img');
    //   img.src = url_image;
    //   img.style.width = '100%';
    //   divBackground.style.background = "none";
    //   divBackground.appendChild(img);
    // }

    const is_not_display_hhmmss = invoice.invoice.is_not_display_hhmmss;
    //Thm vin cho v
    const container: any = doc.querySelector('.container');
    if (container) {
      if (!options || (options && !options.inVe)) {
        container.style.borderRadius = '5px';
        container.style.border = '2px solid rgb(0, 149, 249)';
        container.style.padding = '20px';
        container.style.boxShadow = 'rgba(0, 0, 0, 0.2) 0px 0px 10px';
        container.style.margin = 'auto';
      } else if (options && options.inVe) {
        // set ng vin cho vng k
        const efyEsignNone: any = doc.querySelector('.efy-esign-none');
        if (efyEsignNone) {
          efyEsignNone.style.borderWidth = '2px';
        }
      }
    }

    //invoice-number
    const invoice_number: any = doc.querySelector('.invoice-number');
    if (invoice_number) {
      invoice_number.textContent = invoice.invoice.invoice_number ? invoice.invoice.invoice_number : "";
    }
    const invoice_sign_date: any = doc.querySelector('.efy-sign-date');
    const seler_sign_name: any = doc.querySelector('.seler-sign-name');
    const kySoBenBan: any = doc.querySelector('.ky-so-ben-ban');
    const KylblKySoBenBan: any = doc.querySelector('.da-duoc-ky-so-ben-ban');
    if (options && options.isPreview) {
      kySoBenBan.style.display = "none";
      if (KylblKySoBenBan)
        KylblKySoBenBan.style.display = "none";
    } else {
      // Bn bn k ?
      if (KylblKySoBenBan && !invoice.invoice.is_view_signature) {
        KylblKySoBenBan.style.display = "none";
        kySoBenBan.style.display = "none";

      } else {
        // Ngy k/k bi
        if (invoice_sign_date && invoice.invoice.invoice_sign_date) {
          if (is_not_display_hhmmss) {
            invoice_sign_date.textContent = moment(invoice.invoice.invoice_sign_date).format("DD/MM/YYYY");
          } else {
            invoice_sign_date.textContent = moment(invoice.invoice.invoice_sign_date).format("DD/MM/YYYY HH:mm:ss");
          }
        }
        if (seler_sign_name) {
          seler_sign_name.textContent = invoice.seler.sign_name ? invoice.seler.sign_name : invoice.seler.name;
        }
      }
    }


    // m cqt
    const maCoQuanThue: any = doc.querySelector('.ma-co-quan-thue');
    if (maCoQuanThue) {
      maCoQuanThue.textContent = invoice.invoice.tax_authority_code;
    }
    // thue-mien-giam-nq43
    if (invoice.invoice.is_resolution_43) {
      const mienGiam43: any = doc.querySelector('.thue-mien-giam-nq43');
      if (mienGiam43) {
        mienGiam43.textContent = " gim " + invoice.invoice.money_resolution_43 + this._translate.THONG_TIN.tinh_thue_nq101;
        const parentThueMienGiam: any = doc.querySelector('.parent-thue-mien-giam-nq43');
        if (parentThueMienGiam) {
          parentThueMienGiam.style.display = 'block';
        }
      }
    }


    if (invoice.invoice.invoice_date) {
      this.invoice_date = moment(invoice.invoice.invoice_date).format("DD/MM/YYYY");
    }
    let buyer_email = "";
    if (invoice.customer.email)
      buyer_email = invoice.customer.email.replace(/;/g, '; ');

    // Gi tin
    let total_payment: any = doc.querySelector('.total-payment')
    if (total_payment) {
      total_payment.textContent = invoice.invoice.total_payment
    }
    let arrMap = {
      // 'gia_tien': {
      //   id: 'f0f2a367-8ca8-006a-eb0c-cf3261f00999',
      //   value: invoice.invoice.total_payment
      // },
      'nguoi_xuat': {
        id: 'a188b9ae-40c4-11ee-be56-0242ac120002',
        value: invoice.invoice.user_name_export
      },
      'ho_ten_nguoi_mua': {
        id: '9c103984-576d-c322-7843-bdacf97ac189',
        value: invoice.customer.contact
      },
      'ten_don_vi': {
        id: '8c103984-576d-c322-7843-bdacf97ac189',
        value: invoice.customer.name
      },
      'mst': {
        id: '8d903984-576d-c322-7843-bdacf97ac100',
        value: invoice.customer.tax_number
      },
      'dia_chi': {
        id: '7c103984-576d-c322-7843-bdacf97ac189',
        value: invoice.customer.address
      },
      'email': {
        id: '8e31d4c8-40c2-11ee-be56-0242ac120002',
        value: buyer_email
      },
      'sdt': {
        id: '11111121-549a-01ea-e507-cc7b0ab28a69',
        value: invoice.customer.tel
      },
    }

    // in chuyn i
    if (options && options.inChuyenDoi) {
      let e: any = doc.querySelector('.childparent-ngay_chuyen_doi')
      if (e)
        e.style.display = 'block';
      e = doc.querySelector('.childparent-nguoi_chuyen_doi')
      if (e)
        e.style.display = 'block';
      let textInChuyenDoi: any = doc.querySelector('.childparent-ban-the-hien-ve-the')
      if (textInChuyenDoi) {
        textInChuyenDoi.style.display = 'unset'
      }
      const nguoiChuyenDoi: any = doc.querySelector('.nguoi_chuyen_doi');
      if (nguoiChuyenDoi) {
        if (this.configs && !this.configs.system_parameter.dont_display_conversion_name) {
          nguoiChuyenDoi.textContent = invoice.invoice.name_print_convert;
          console.log(123);
        } else {
          if (e)
            e.style.display = 'none';
        }
      }

      const ngayChuyenDoi = doc.querySelector('.ngay_chuyen_doi');
      if (ngayChuyenDoi && invoice.invoice.date_print_convert) {
        ngayChuyenDoi.textContent = moment(invoice.invoice.date_print_convert).format("DD/MM/YYYY HH:mm");
      }
    }


    for (const field in arrMap) {
      const { id, value } = arrMap[field]
      let xElements = doc.querySelector(`.value-${id}`);
      if (xElements) {
        xElements.textContent = value
      }
    }

    const dateString = invoice.invoice.invoice_date;
    if (dateString) {
      const dateObject = new Date(dateString);
      const year = dateObject.getFullYear();
      const month = dateObject.getMonth() + 1;
      const day = dateObject.getDate();
      let xElements = doc.querySelector('.NgayHD');
      xElements.textContent = `${day}`;
      xElements = doc.querySelector('.ThangHD');
      xElements.textContent = `${month}`;
      xElements = doc.querySelector('.NamHD');
      xElements.textContent = `${year}`;
    }

    // Trng m rng bn mua
    if (invoice.invoice.invoice_options.length > 0) {
      const inv_options = invoice.invoice.invoice_options;
      inv_options.forEach(option => {
        let xElements = doc.querySelector(`.value-${option.code}`);
        if (xElements) {
          let value = '';
          if (option.data_type == 'ngay' && option.value) {
            value = moment(option.value).format("DD/MM/YYYY");
          } else {
            value = option.value;
          }
          xElements.textContent = value;
        }
      })
    }

    // Trng m rng hng ho
    if (!is_multi_product) {

      if (invoice.order.length > 0) {
        if (invoice.order[0].extension && invoice.order[0].extension.length > 0) {
          const pro_options = invoice.order[0].extension;
          pro_options.forEach(option => {
            let xElements = doc.querySelector(`.value-${option.code}`);
            if (xElements) {
              let value = '';
              if (option.data_type == 'ngay' && option.value) {
                value = moment(option.value).format("DD/MM/YYYY");
              } else {
                value = option.value;
              }
              xElements.textContent = value;
            }
          })
        }
        // Tn hng ho
        let xElements = doc.querySelector(`.value-8l873098-576d-c322-7843-bdacf97ac100`);
        if (xElements) {
          xElements.textContent = invoice.order[0].item_name;
        }
      }
    }
    let qrcodeElement: any = doc.querySelector('.qrcode');
    if (invoice.invoice.qr_code) {
      let qrcode = this.generateQRCode(invoice.invoice.qr_code)
      if (qrcodeElement && qrcode) {
        qrcodeElement.style.backgroundImage = 'url(' + qrcode + ')';
        // qrcodeElement.style.backgroundRepeat = 'no-repeat';
      }
    }

    // m tra cu
    const matracuu: any = doc.querySelector('.matracuu');
    if (matracuu && invoice.invoice.invoice_code && invoice.invoice.invoice_number) {
      const link = doc.createElement('a');
      link.href = `https://ihoadon.vn/kiem-tra/?shd=${invoice.invoice.invoice_number}&mtc=${invoice.invoice.invoice_code}`;
      link.textContent = invoice.invoice.invoice_code;
      link.target = "_blank";
      link.style.textDecoration = "none";
      matracuu.textContent = '';
      matracuu.appendChild(link);
    }

    // Nu cha c sinh s
    if (!invoice.invoice.invoice_number) {
      if (kySoBenBan)
        kySoBenBan.style.display = "none";
      if (KylblKySoBenBan)
        KylblKySoBenBan.style.display = "none";
      if (matracuu)
        matracuu.style.display = "none";
      // const maqrcode: any = doc.querySelector('.ma-qr-code')
      // if (maqrcode)
      //   maqrcode.style.display = "none";
    }

    return doc.documentElement.outerHTML;
  }
  generateQRCode(qr) {
    let qrcode: any = null;
    const qrcodeOptions = {
      width: 140,
      height: 140
    }; // Kch thc ca QR code
    QRCode.toDataURL(qr, qrcodeOptions, (err, url) => {
      if (err) throw err;
      qrcode = url;
    });
    return qrcode;
  }
}
