  setCurrencyTicket() {
    // if (this.configs.system_parameter.ticket_currency != this.currency_name) {
    this.configs.system_parameter["ticket_currency"] = this.currency_name;
    this.configs.system_parameter["is_ticket_currency"] = this.is_currency;
    const id = this.configs.id.system_parameter;
    const data = {
      config: {
        value: JSON.stringify(this.configs.system_parameter),
        is_no_add_log: true
      }
    };
    this.vatService.updateConfig(id, data).subscribe(res => {
      if (res.status == 'success') {
        this.configsService.getAll().subscribe((res) => {
          if (res.data && res.data.configs)
            this.authService.setConfigs(res.data.configs);
          this.configs = this.authService.getConfigs();
        });
      }
    });
    // }
  }

  trangthaihoadon(data) {
    switch (data.status) {
      case 'DA_XUAT':
        break;
      case 'GHI_TAM':
        if (data.invoice_number) {
          return this._translate.GRID.INVOICE.signed_error;
        } else {
          return this._translate.CONTROLL.BUTTON.save_temporary;
        }
      case 'GIU_SO':
        return this._translate.GRID.INVOICE.wait_for_sign;
      default:
        break;
    }
  }


  /* X l ghi tm ha n */
  processGhiTam(type) {
    if (!this.isAccessExportInv) {
      if (type === sEnum.statusInv.TrinhKy) {
        this.form_submit.approve_status = sEnum.statusApprove.ChoDuyet;
        this.form_submit.refuse_reason = '';
      } else {
        if (this.form_submit.approve_status !== sEnum.statusApprove.TuChoi) {
          this.form_submit.approve_status = sEnum.statusApprove.ChuaTrinhKy;
        }
      }
    }
    this.ghiTam(this.form_submit);
  }

  /* Ghi tm */
  ghiTam(data) {
    if (this.frmData.id) {
      Library.showloading();
      const sub = this.vatService.editTT78(this.form_submit.id, data).subscribe(rs => {
        if (rs.status === 'success') {
          this.frmData.id = rs.data.id;
          if (this.frmData.approve_status === sEnum.statusApprove.ChoDuyet) {
            Library.notify(this._translate.NOTIFY.sign_board_success, 'success');
          } else {
            Library.notify(this._translate.NOTIFY.save_invoice_success, 'success');
          }
          this.setCurrencyTicket();
          this.setDataSessionStorage(this.frmData);
        } else {
          if (rs.message.includes('CHUA_CO_BIEN_GIAM_TRU')) {
            const initialState = {
              typeNotify: "MAU_HOA_DON",
            };
            this.bsModalRef = this.modalService.show(NotifyReduceTaxComponent, {
              backdrop: 'static',
              keyboard: false,
              class: 'mauhoadon modal-lg',
              initialState
            });
          }
          else if (rs.message.includes('MAU_HOA_DON_NQ43')) {
            const initialState = {
              typeNotify: "MAU_HOA_DON_NQ43",
            };
            this.bsModalRef = this.modalService.show(NotifyReduceTaxComponent, {
              backdrop: 'static',
              keyboard: false,
              class: 'mauhoadon43 modal-lg',
              initialState
            });
          }
          else
            Library.notify(rs.message, 'error');
        }
      }, err => {
      }, () => {

        Library.hideloading();
        sub.unsubscribe();
      });

    } else {
      Library.showloading();
      const sub = this.vatService.ghiTamTT78(data).subscribe(rs => {
        if (rs.status === 'success') {
          // if (rs.data.status == sEnum.statusInv.ChoKy)
          //   this.isShowButtonWaiteSign = false;
          this.setCurrencyTicket();
          this.frmData.id = rs.data.id;
          // this.pkInvoiceSelectWaitSign = this.frmData.id;
          Library.notify(this._translate.NOTIFY.save_invoice_success, 'success');
          this.setDataSessionStorage(this.frmData);
        } else {
          if (rs.message.includes('CHUA_CO_BIEN_GIAM_TRU')) {
            const initialState = {
              typeNotify: "MAU_HOA_DON",
            };
            this.bsModalRef = this.modalService.show(NotifyReduceTaxComponent, {
              backdrop: 'static',
              keyboard: false,
              class: 'mauhoadon modal-lg',
              initialState
            });
          }
          else if (rs.message.includes('MAU_HOA_DON_NQ43')) {
            const initialState = {
              typeNotify: "MAU_HOA_DON_NQ43",
            };
            this.bsModalRef = this.modalService.show(NotifyReduceTaxComponent, {
              backdrop: 'static',
              keyboard: false,
              class: 'mauhoadon43 modal-lg',
              initialState
            });
          }
          else {
            Library.notify(rs.message, 'error');
          }
        }
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    }
  }

