import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { sEnum } from '@config/sEnum';
import { Helper, VPreviewInvService, VSelectBoxService, VStringService } from '@core/index';
import { Library } from '@core/library';
import { AuthService, SocketService } from '@core/services';
import { VatService } from '@modules/vat/shared';
import { TranslateService } from '@ngx-translate/core';
import { SendDataForTaxComponent, ViewInvoiceMultipageComponent } from '@shared/components';
import { DownloadPdfComponent } from '@shared/components/download-pdf/download-pdf.component';
import { SettingColumnComponent } from '@shared/components/setting-column/setting-column.component';
import { SignatureOffComponent } from '@shared/index';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { NgxSpinnerService } from 'ngx-spinner';
