import { Component, OnInit, Input } from '@angular/core';
import { Library } from '@core/library';
import { AuthService } from '@core/services';
import { CustomersService } from '@modules/customers/shared';
import { TemplateService } from '@modules/template/shared';
import { TicketService } from '@modules/ticket/shared/ticket.service';
import { VatService } from '@modules/vat/shared';
import { PreviewTicketComponent } from '@shared/components/preview-ticket/preview-ticket.component';
import ArrayStore from 'devextreme/data/array_store';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';

@Component({
  selector: 'app-many-receive',
  templateUrl: './many-receive.component.html',
  styleUrls: ['./many-receive.component.scss']
})
export class ManyReceiveComponent implements OnInit {

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private customersService: CustomersService,
    private vatService: VatService,
    private ticketService: TicketService,
    private modalService: BsModalService,
    private templateService: TemplateService,
  ) { }

  @Input() invoiceList: any[];
  _translate: any
  invoice_list: ArrayStore;
  arrInvoiceList: string;
  ngOnInit() {
    this.invoice_list = new ArrayStore({
      key: 'id',
      data: this.invoiceList
    });
    this._translate = this.authService.getTranslate();
    this.arrInvoiceList = JSON.stringify(this.invoiceList);
  }

  view(data) {
    Library.showloading();
    let is_multi_product = false;
    if (data.id && data.template_id) {
      const temp = this.templateService.show(data.template_id).subscribe(rbs => {
        if (rbs.status === 'success') {
          let invoiceTemplate = rbs.data.template;
          const sub = this.vatService.view(data.id, true).subscribe(rs => {
            if (rs.status === 'success') {
              if (rs.data.data.item && rs.data.data.item.length > 0) {
                let invoice = rs.data.data.item[0];
                const rawHtml = this.ticketService.createFormTypeBase64(invoice, invoiceTemplate, is_multi_product);
                const initialState = {
                  dataPreview: rawHtml + '</html>',
                  isPreview: true
                };
                this.modalService.show(PreviewTicketComponent, {
                  class: 'modal-lg full-modal-ticket1',
                  initialState,
                  backdrop: 'static',
                  keyboard: false
                });
              }
            }
          },
            (err) => { Library.notify(err.message, 'error'); },
            () => { Library.hideloading(); }
          )
        }
      },
        (err) => {
          Library.notify(err.message, 'error');
        },
        () => { temp.unsubscribe() }
      )
    }
  }
  dong() {
    this.bsModalRef.content.successSaveItem = 'success';
    this.bsModalRef.hide();
  }

  _validForm() {
    let invoices = this.invoiceList.filter(item => item.buyer_email);
    return invoices.length == this.invoiceList.length;
  }


  async dongy() {
    const data: any = [];
    if(!this._validForm()){
      Library.notify("Email" + ' ' + this._translate.VALIDATION.required, 'error');
      return false;
    }
    this.invoiceList.forEach(item => {
      data.push({ id: item.id, email: item.buyer_email });
    });
    Library.showloading();
    await this.vatService.sendMail(data).toPromise()
      .then(
        rs => {
          if (rs.status === 'success') {
            Library.notify(this._translate.CONTROLL.LABEL.send_access_email, 'success');
            this.bsModalRef.content.successSaveItem = 'success_mail';
            this.bsModalRef.hide();
            Library.hideloading();
            return true;
          } else {
            Library.notify(rs.message, 'error');
            Library.hideloading();
            return false;
          }
        },
        reason => {
          Library.hideloading();
          Library.notify(reason, 'error');
          return false;
        }
      );
  }

}
