import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './components/index/index.component';
import { InvoiceComponent } from './components/invoice/invoice.component';
import { VatDeactivateGuard } from '@modules/vat/deactivate/vat-deactivate.guard';
import { TicketResolver, TicketResolverIndex } from './shared/ticket.resolver';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent,
    resolve: { init: TicketResolverIndex },
  },
  {
    path: 'create',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: TicketResolver },
    data: { brec: 'create_ticket' }
  },
  {
    path: ':invoiceId',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: TicketResolver},
    data: {brec: 'invoice_details'}
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TicketRoutingModule { }
