import { Component, OnInit } from '@angular/core';
import { pattern } from '@config/pattern';
import { Library } from '@core/library';
import { AuthService } from '@core/services';
import { CustomersService } from '@modules/customers/shared';
import { VatService } from '@modules/vat/shared';
import { BsModalRef } from 'ngx-bootstrap';

@Component({
  selector: 'app-send-email',
  templateUrl: './send-email.component.html',
  styleUrls: ['./send-email.component.scss']
})
export class SendEmailComponent implements OnInit {

  invoice_id: any;
  frmData: any = {};
  email: string;
  customer_id: string;
  pattern = pattern;
  _translate: any;

  constructor(
    public bsModalRef: BsModalRef,
    private vatService: VatService,
    private authService: AuthService,
    private customersService: CustomersService,
  ) {
    this._translate = this.authService.getTranslate();

  }

  ngOnInit() {
    this.frmData.email = this.email;
  }

  onSubmit(e) {
    let data = [{
      id: this.invoice_id,
      email: this.frmData.email
    }];
    Library.showloading();
    let sub = this.vatService.sendMail(data).subscribe(rs => {
      if (rs.status == 'success') {
        let updateEmail = {
          id: this.customer_id,
          email: this.frmData.email
        };
        this.customersService.uploadEmailtoCustomer(updateEmail).subscribe(rsl => {
          if (rsl.status == 'success') {

          }
        });
        Library.notify(this._translate.NOTIFY.send_mail_success, 'success');
        this.bsModalRef.content.successSaveItem = "success_mail";
        this.bsModalRef.hide();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

}
