import { Component, OnInit, Input } from '@angular/core';
import { AuthService } from '@core/services';
import ArrayStore from 'devextreme/data/array_store';
import { BsModalRef } from 'ngx-bootstrap';
import * as _ from 'lodash';
import { Library } from '@core/library';
import { VatService } from '@modules/vat/shared';

@Component({
  selector: 'app-one-receive',
  templateUrl: './one-receive.component.html',
  styleUrls: ['./one-receive.component.scss']
})
export class OneReceiveComponent implements OnInit {

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private vatService: VatService,
  ) { }

  frmData: any = {};
  @Input() invoiceList: any[];
  _translate: any
  invoice_list: ArrayStore
  ngOnInit() {
    this.invoice_list = new ArrayStore({
      key: 'id',
      data: this.invoiceList
    });
    this._translate = this.authService.getTranslate();
  }

  onEditingStart(e) {
    // e.component.saveEditData();
  }
  selectionChanged(data: any) {

  }
  onEditorPrepared(e) { }
  onEditorPreparing(e) { }
  view(data) { }
  dong() {
    this.bsModalRef.content.successSaveItem = 'success';
    this.bsModalRef.hide();
  }
  dongy() {
    if (this.invoiceList.length > 0) {
      const ids = _.map(this.invoiceList, 'id');
      if (!this._validForm())
        return false;
      Library.showloading();
      let data = {
        ids: ids,
        email: this.frmData.email,
        buyer_name: this.frmData.username
      }
      let sub = this.vatService.sendEmailOneReceive(data).subscribe(rs => {
        if (rs.status == 'success') {
          Library.notify(this._translate.CONTROLL.LABEL.send_access_email, 'success');
            this.bsModalRef.content.successSaveItem = 'success_mail';
            this.bsModalRef.hide();
            Library.hideloading();
        }
      },
        (err) => {
          Library.notify(err.message, 'error')
        },
        () => {
          sub.unsubscribe();
          Library.hideloading();
        }
      )
    }
  }
  _validForm() {
    if (!this.frmData.username) {
      Library.notify(this._translate.CONTROLL.LABEL.name_receiver + " " + this._translate.VALIDATION.required, 'error');
      return false;
    }
    if (!this.frmData.email) {
      Library.notify(this._translate.CONTROLL.LABEL.email + " " + this._translate.VALIDATION.required, 'error');
      return false;
    }
    return true;
  }

}
